h1,h2,h3,h4,h5,h6 {
  @include t-font--heading;
}

h1 {
  @include t-font--heading-1;
}

h2 {
  @include t-font--heading-2;
}

h3 {
  @include t-font--heading-3;
}

h4 {
  @include t-font--heading-4;
}

h5 {
  @include t-font--heading-5;
}

h6 {
  @include t-font--heading-6;
}

@include t-media-query($s-medium-up) {
  h1 {
    @include t-font--heading-1-laptop;
  }

  h2 {
    @include t-font--heading-2-laptop;
  }

  h3 {
    @include t-font--heading-3-laptop;
  }

  h4 {
    @include t-font--heading-4-laptop;
  }

  h5 {
    @include t-font--heading-5-laptop;
  }
}

@include t-media-query($s-xlarge-up) {
  h1 {
    @include t-font--heading-1-desktop;
  }

  h2 {
    @include t-font--heading-2-desktop;
  }

  h3 {
    @include t-font--heading-3-desktop;
  }

  h4 {
    @include t-font--heading-4-desktop;
  }

  h5 {
    @include t-font--heading-5-desktop;
  }

  h6 {
    @include t-font--heading-6-desktop;
  }
}