@import "../base";

.c-gwp-message {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
  background: $s-color--mine-shaft;
  color: $s-color--font-invert;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem 0;
  opacity: 0;
  transition: all 0.2s;
  z-index: $s-z--cart-drawer-tooltip;
  text-align: center;
  width: 100%;

  &.is-visible {
    opacity: 1;
    transform: translate(-50%, 0);
  }

  @include t-media-query($s-small-up) {
    bottom: 2rem;
    width: auto;
    transform: translate(-50%, 150%);
  }
}
