.o-heading,
.o-heading--1,
.o-heading--2,
.o-heading--3,
.o-heading--4,
.o-heading--5,
.o-heading--6 {
  @include t-font--heading;
}

.o-heading--1,
.o-heading.is-size-1 {
  @include t-font--heading-1;
}

.o-heading--2,
.o-heading.is-size-2 {
  @include t-font--heading-2;
}

.o-heading--3,
.o-heading.is-size-3 {
  @include t-font--heading-3;
}

.o-heading--4,
.o-heading.is-size-4 {
  @include t-font--heading-4;
}

.o-heading--5,
.o-heading.is-size-5 {
  @include t-font--heading-5;
}

.o-heading--6,
.o-heading.is-size-6 {
  @include t-font--heading-6;
}

.o-paragraph {
  @include t-font--paragraph-1;
}

.o-paragraph--2 {
  @include t-font--paragraph-2;
}

/*** Media Queries ***/

@include t-media-query($s-medium-up) {

  .o-heading--1,
  .o-heading.is-size-1 {
    @include t-font--heading-1-laptop;
  }

  .o-heading--2,
  .o-heading.is-size-2 {
    @include t-font--heading-2-laptop;
  }

  .o-heading--3,
  .o-heading.is-size-3 {
    @include t-font--heading-3-laptop;
  }

  .o-heading--4,
  .o-heading.is-size-4 {
    @include t-font--heading-4-laptop;
  }

  .o-heading--5,
  .o-heading.is-size-5 {
    @include t-font--heading-5-laptop;
  }

  .o-heading--6,
  .o-heading.is-size-6 {
    @include t-font--heading-6-laptop;
  }

  .o-paragraph {
    @include t-font--paragraph-1-laptop;
  }
}

@include t-media-query($s-xlarge-up) {
  .o-heading--1,
  .o-heading.is-size-1 {
    @include t-font--heading-1-desktop;
  }

  .o-heading--2,
  .o-heading.is-size-2 {
    @include t-font--heading-2-desktop;
  }

  .o-heading--3,
  .o-heading.is-size-3 {
    @include t-font--heading-3-desktop;
  }

  .o-heading--4,
  .o-heading.is-size-4 {
    @include t-font--heading-4-desktop;
  }

  .o-heading--5,
  .o-heading.is-size-5 {
    @include t-font--heading-5-desktop;
  }

  .o-heading--6,
  .o-heading.is-size-6 {
    @include t-font--heading-6-desktop;
  }

  .o-paragraph {
    @include t-font--paragraph-1-desktop;
  }
}