.o-wysiwyg {
  ul, ol {
    padding-left: 1.3125rem;
  }

  li,
  td {
    @include t-font--paragraph-1;
  }

  p:first-of-type {
    margin-top: 0;
  }

  p:last-of-type {
    margin-bottom: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 1.5rem 0;

    &:first-of-type {
      margin-top: 0;
    }
  }

  a {
    color: $s-color--font-link;
    padding-bottom: 1px;
    border-bottom: 1px solid $s-color--font-link;
  }

  table {
    border: 1px solid $s-color--border-subdued;
  }

  tr td {
    border-bottom: 1px solid $s-color--border-subdued;
  }

  table tr:last-child td {
    border-bottom: none;
  }

  table tr td {
    border-right: 1px solid $s-color--border-subdued;
    padding: 0.5rem;
  }

  table tr td:last-child {
    border-right: none;
  }

  /* Media Queries */

  @include t-media-query($s-small-up) {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 2rem 0;
    }
  }

  @include t-media-query($s-medium-up) {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 3rem 0;
    }
  }

  @include t-media-query($s-large-up) {
    li,
    td {
      @include t-font--paragraph-1-desktop;
    }
  }
}