html,
body {
  margin: 0;
  padding: 0;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smooth: antialiased;

  @include t-font--root;
  color: $s-color--font;

  &.is-scroll-disabled {
    overflow: hidden;
  }

  /**
    Accessibility focus styles
  */
  *:focus {
    outline: $s-color--border-focus solid 0.0625rem;
  }

  .js-focus-visible :focus:not(.focus-visible) {
    outline: none;
  }

  *:focus:not(:focus-visible) {
    outline: none;
  }

  *:focus-visible {
    outline: $s-color--border-focus solid 0.0625rem;
  }

  .js-focus-visible :focus:not(.focus-visible) {
    outline: none;
  }
}

#overlay {
  @include t-anim--transition(opacity, $ease: linear);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $s-z--overlay;
  background-color: $s-color--overlay;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;

  .has-overlay-open & {
    opacity: 1;
    pointer-events: initial;
  }
}
