.wishlistpage {
  margin-top: 2.5rem;
}

.wishlisttitlecontainer {
  .titletext {
    font-size: $s-font--heading-1-size;
    font-weight: $s-font--root-weight;
    font-family: $s-font--heading-family;
    line-height: $s-font--root-line;
  }

  a {
    border-radius: 3.125rem !important;
  }

}

.wishlistbodycontainer {
  font-family: $s-font--root-family !important;

  .product_title {
    @include t-font--heading-6;
  }

  .buttons {
    @include t-font--link;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    gap: 0.625rem;

    a {
      border-radius: 3.125rem;
    }
  }
}

.modal-footer {
  button {
    border-radius: 3.125rem !important;
    color: #fff;

  }
}

.empty-wishlist {
  font-family: $s-font--root-family !important;
}

.is-drawer-open #smile-ui-container {
  display: none;
}

@include t-media-query($s-small-up) {
  .is-drawer-open #smile-ui-container {
    display: block;
  }
}

@include t-media-query($s-medium-up) {
  .wishlistpage {
    margin-top: 5rem;
  }

  #heading-breadcrumbs {
    margin-bottom: 2.5rem;
  }
}