.o-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 9.75rem;
  padding: 0.75rem 2.5rem;
  font-size: $s-font--btn-size;
  line-height: $s-font--btn-line;
  cursor: pointer;
  border-radius: 3.125rem;


  // Container
  &__container {
    margin-top: 3.125rem;
  }

  // Primary
  background-color: $s-color--btn-background;
  border: solid 0.0625rem $s-color--border-clear;
  color: $s-color--btn-font;
  border-radius: 6.25rem;

  &.is-alternative {
    color: $s-color--btn-font-alt;
    background-color: $s-color--btn-background-alt;
  }

  // Secondary
  &.is-secondary {
    position: relative;
    padding: 0.7188rem 1.25rem 0.9688rem;
    min-width: 6.875rem;
    font-size: $s-font--btn-secondary-size;
    line-height: $s-font--btn-secondary-line;
    text-decoration: underline;
    background-color: transparent;
    @include t-anim--transition(opacity, $ease: linear);

    &:not(.is-disabled):hover {
      opacity: 0.9;
    }
  }

  &__shape {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: $s-z--reset;
    transition: fill .2s;

    // Default colour
    fill: $s-color--btn-background;

    // Product colour overrides
    @each $name, $value in $brand-colors {
      .is-colour-#{$name} & {
        fill: map-deep-get($brand-colors, $name, 'primary');
      }
    }

    &.is-desktop {
      display: none;
    }
  }

  &__label {
    position: relative;
    z-index: $s-z--text-overlay;
  }

  &.is-full {
    display: flex;
    width: 100%;
  }

  &.is-large {
    padding: 1.0938rem 3.125rem;
  }

  // sold out button colour 
  &.is-disabled {
    cursor: not-allowed;

    .o-btn__shape {
      fill: $s-color--btn-background-disable;
    }
  }

  &.is-added{
    .o-btn__shape {
      fill: $s-color--btn-background-added;
    }
  }

  // Colour classes
  @each $name, $value in $brand-colors {
    $brand-font-color: map-deep-get($brand-colors, $name, 'secondary');
    @if $brand-font-color != $s-color--btn-font {
      &.is-colour-#{$name} {
        color: $brand-font-color;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    // Hover state
    // &:hover{

    // }

    &.is-secondary {
      padding: 1.4844rem 1.5rem;
      min-width: 4.125rem;
    }

    &__shape {
      &.is-desktop { display: inline-flex; }
      &.is-mobile { display: none; }
    }
  }

  @include t-media-query($s-xlarge-up) {
    min-width: 10.375rem;
    padding: 1rem 3.125rem;
    font-size: $s-font--btn-size-desktop;

    // Container
    &__container {
      margin-top: 5rem;
    }

    &.is-secondary {
      padding: 2rem 1.75rem;
      min-width: 6.25rem;
      font-size: $s-font--btn-secondary-size-desktop;
    }
  }
}