.o-wave {
  display: block;
  width: 100%;
  overflow: hidden;
  margin: -0.125rem 0;
  transform: translateY(1px);

  &__wave {
    width: 200%;
    display: block;
    transition: transform 2s ease-out;

    .o-wave.is-visible & {
      transform: translateX(-50%);
    }

    .is-flipped & {
      transform: rotate(180deg);
    }

    .o-wave.is-visible.is-flipped & {
      transform: rotate(180deg) translateX(50%);
    }
  }
}