.o-input {
  // Fix iOS Shadows
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  // Fix webkit autofill
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
    box-shadow: 0 0 0 100vw $s-color--background inset !important;
  }

  // Display Related
  position: relative;
  display: inline-block;
  width: 100%;
  background-color:$s-color--background-focus;
  padding:1rem 1.5rem;

  // Typography

  &::placeholder {}

  resize: none;

  &:focus-visible {
    outline: none;
    // border-color: darken($s-color--background, 8%);
  }

  *+& {
    margin-top: 0.625rem;
  }

  // Primary style
  &.is-primary {
    @include t-font--paragraph-2;
    box-sizing: border-box;
    padding: 1rem 0 1rem 1.5rem;
    border: 0.0625rem solid $s-color--background-focus;
    border-radius: 0.25rem;
    color: $s-color--mine-shaft;
    opacity: 0.8;
    background-color: $s-color--background-focus;

    &:focus,
    &:hover {
      border-color: $s-color--border-focus;
    }
  }


  // Secondary style
  &.is-secondary {
    @include t-font--paragraph-1;
    padding: 0.625rem 1.5625rem 0.625rem 0;
    border-bottom: solid 0.0625rem $s-color--border-subdued-20;

    &::placeholder {
      color: $s-color--font-subdued-20;
    }
  }

  &.is-select {
    color: inherit;
    background-repeat: no-repeat;
    background-size: 0.75rem 0.75rem;
    background-position: right 1.25rem center;

    &::placeholder {
      color: inherit;
    }
  }

  &.has-error {
    border-color: $s-color--error;

    &:focus {
      // border-color: $s-color--input-border;
    }
  }

  /*** Media Queries ***/
  @include t-media-query($s-medium-up) {
    &.is-secondary {
      @include t-font--paragraph-1-laptop;
      padding: 0.75rem 2.3125rem 0.75rem 0;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &.is-secondary {
      @include t-font--paragraph-1-desktop;
    }
  }
}