@import '../base';

.shopify-challenge__container{
  padding: 11rem 0;
  display: grid;
  justify-content: center;

  input.shopify-challenge__button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 9.75rem;
    padding: 0.75rem 2.5rem;
    font-size: $s-font--btn-size;
    line-height: $s-font--btn-line;
    cursor: pointer;
    border-radius: 3.125rem;
    background-color: $s-color--btn-background;
    border: solid 0.0625rem $s-color--border-clear;
    color: $s-color--btn-font;
    border-radius: 6.25rem;
    margin: 0 auto;
    width: 100%;
  }

  .g-recaptcha {
    margin: 1rem 0 2rem;
  }
}