/* This is the secondary button style from the designs style guide */
.o-link {
  @include t-font--link;
  position: relative;
  padding-bottom: 0.0625rem;
  cursor: pointer;
  color: $s-color--font-focus;

  &::before {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.0625rem;
    background: $s-color--border-focus;
    content: '';
  }

  &.is-alternative {
    color: $s-color--font-invert;

    &::before {
      background-color: $s-color--border-invert;
    }
  }

  &__container {
    margin-top: 1.25rem;
  }


  /*** Media Queries ***/

  @include t-media-query($s-xlarge-up) {
    @include t-font--link-desktop;

    &__container {
      margin-top: 1.875rem;
    }
  }
}